.wrapper {
  position: relative;
  height: 100vh; }

.controls {
  position: absolute;
  top: 0.25rem;
  right: 0.5rem;
  z-index: 2; }
  .controls .btn {
    cursor: pointer; }

.tl-progress-count {
  display: inline-block;
  border-radius: 0;
  border-bottom: solid 0.2rem rgba(0, 0, 0, 0.54);
  margin-left: 0.5rem;
  text-align: center;
  width: 4.25rem;
  padding-bottom: 0;
  padding-top: 0; }

.tl-progressbar {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 2rem;
  margin: 0 1.25rem 0.75rem 1.25rem; }
  .tl-progressbar .indicator {
    background: #0277BD;
    border-radius: 0.2rem;
    position: absolute;
    top: 50%;
    width: 0.5rem;
    height: 2rem;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%); }
  .tl-progressbar hr {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    margin: 0;
    border-color: #0277BD; }

.box {
  position: fixed;
  left: 1rem;
  top: 1rem;
  right: 1rem;
  bottom: 1rem;
  border: solid 0.2rem #0277BD;
  border-radius: 0.4rem; }

.smallbox {
  background: #0277BD;
  width: 5rem;
  height: 5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%); }



